<template>
    <div>
      <MHeader title="列表页"></MHeader>
        <scroller
          :on-refresh="refresh" ref="scroll" class="scroller">
          <ul>
            <li v-for="list in lists">
              <img v-lazy="list.bookCover" lazy="error" alt="" >
              <div class="content-btn">
                <h4>{{list.bookName}}</h4>
                <p>{{list.content}}</p>
                <i class="iconfont icon-zan" @click="collect(list)"></i>
                <div class="btn-list">
                  <router-link :to="{path:'/update/'+list.id}" tag="button">修改</router-link>
                  <button @click="remove(list.id)">删除</button>
                </div>
              </div>
            </li>
          </ul>
        </scroller>
    </div>
</template>
<script>

  import MHeader from 'components/header'
  import {getLists,delBook} from 'api'
  import {mapMutations} from 'vuex'
    export default {
        data(){
            return {lists:[]}
        },
        components: {MHeader},
        created(){
          getLists().then(res=>{
            this.lists = res.data;
          })
        },
        methods: {
          ...mapMutations(['add']),
          remove(id){
            delBook(id).then(()=>{
              this.lists = this.lists.filter((item)=>item.id != id);
            });
          },
          collect(list){
            this.add(list);
            this.$router.push('/collect')
          },
          refresh(){
            getLists().then(res=>{
              this.lists = res.data;
              this.$refs.scroll.finishPullToRefresh();
            })
          }
        }
    }
</script>
<style scoped lang="less">
  .scroller{margin-top: 44px;}
  ul{
    li{
      display: flex;
      flex-direction: row;
      border-bottom:1px solid #ccc;
      padding:10px;
    }
    img{width: 100px;height: 80px;}
    img[lazy=loading]{
      /*width: 100px;*/
      background-position:center center!important;
      background: url("../assets/loading.gif");
      background-size:100px 100px;
      background-repeat:no-repeat;
      -webkit-background-size: cover;
      -moz-background-size: cover;
      -o-background-size: cover;
      background-size: cover;
    }
    img[lazy=loaded]{
      //your code
      animation:fade 0.5s;
    }
    @keyframes fade {
      0%{
        opacity: 0;
      }
      100%{
        opacity: 1;
      }
    }
    .content-btn{
     .btn-list{
       padding-top: 10px;
       button{
         width: 60px;height: 25px;
       }
       button:nth-child(1){
       }
     }

    }
  }
</style>



