<template>
  <div>
    <MHeader title="收藏列表"></MHeader>
    <div class="collect">
      <div v-for="collect in collects" class="collect-list-item">
        <img :src="collect.bookCover" alt="">
        <a @click="cancel(collect)">取消收藏</a>
      </div>
    </div>
    <div v-if="!collects.length" class="empty">

      <p>空空如也~~~,快去收藏</p>
    </div>
  </div>
</template>
<script>
  import {mapState,mapMutations} from 'vuex';
  import MHeader from 'components/header'
    export default {
        data(){
            return {}
        },
        computed:{
          ...mapState(['collects'])
        },
        components: {MHeader},
        methods: {
          ...mapMutations(['remove']),
          cancel(collect){
              this.remove(collect)
          }
        }
    }
</script>
<style scoped lang="less">
  .collect{
    img{width: 100px;height: 80px;}
    .collect-list-item{
      border-bottom: 1px solid #ccc;
      height: 120px;
      display: flex;
      align-items: center;
    }
  }
  .empty{
     text-align: center;
     line-height: 60px;
     color: #ccc;
   }
</style>



