<template>
  <div>
    <MHeader title="修改图书"></MHeader>
    <div>
      <div class="group">
        <label for="bookName">书名</label>
        <input type="text" id="bookName" placeholder="请输入书名" v-model="book.bookName">
      </div>
      <div class="group">
        <label for="bookCover">书的封面</label>
        <input type="text" id="bookCover" placeholder="请输入封面" v-model="book.bookCover">
      </div>
      <div class="group">
        <button @click="update">确认修改</button>
      </div>
    </div>
  </div>
</template>
<script>
  import {updateBook,findOneBook} from 'api'
  import MHeader from 'components/header'
  export default {
    data(){
      return {book:{bookName:'',bookCover:''}}
    },
    components: {MHeader},
    created(){
      findOneBook(this.$route.params.id).then(res=>{
          this.book = res.data;
      });
    },
    methods: {
      update(){
        updateBook(this.$route.params.id,this.book).then(data=>{
          this.$router.push('/list')
        })
      }
    }
  }
</script>
<style scoped lang="less">
  .group{
    padding: 10px;
    display: flex;
    flex-direction: column;
    button{
      height: 40px;
    }
    input{
      border: 1px solid #ccc;
      height: 30px;
      margin:5px  0 ;
    }

  }
</style>



