<template>
  <div v-if="sliders.length">
    <m-header title="珠峰书城"></m-header>
    <slider :data="sliders"></slider>
    <div>
      <h3>最新上映</h3>
      <ul class="book-list" v-if="books.length">
        <li v-for="book in books">
          <img :src="book.bookCover" alt="">
          {{book.bookName}}
        </li>
      </ul>
      <Loading v-if="!books.length"></Loading>
    </div>
  </div>
</template>
<script>
import Slider from 'components/slider'
import Header from 'components/header'
import Loading from 'components/loading'
import {getSlider,getHot} from 'api'
export default {
    data(){
        return {
            sliders:[],
            books:[],
        }
    },
    created(){
      getSlider().then((res)=>{
          this.sliders = res.data;
      });
      getHot().then((res)=>{
          this.books = res.data;
      })
    },
    components: {Slider,Loading,MHeader:Header},
    methods: {}
}
</script>
<style scoped lang="less">
h3{font-size: 16px;padding-left: 2%;color: #666;line-height: 24px;margin: 10px;}
.book-list{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  text-align: center;
  li{
    width: 33.333%;
    img{
      width: 100%;
    }
  }
}
</style>



