import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex)
let state = {
  collects:[]
};
let mutations = {
  remove(state,obj){
    state.collects = state.collects.filter(item=>{
      return item.id !=obj.id
    })
  },
  add(state,obj){
    let flag = state.collects.some(item=>obj.id===item.id);
    if(!flag){
      state.collects.push(obj);
    }

  }
};
let store = new Vuex.Store({
  state,
  mutations
});

export default store



